.partner__media_and_text {
    background-blend-mode: normal !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-attachment: fixed !important;
    position: relative;
    max-height: 750px;
    margin-bottom: 100px;
    padding: 60px 0;
    &.style1 {
        .media-and-text-section-two__top-wave {
            height: 250px;
            background-repeat: no-repeat;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    .container {
        width: 100%;
        max-width: 1200px;
        margin-right: auto;
        margin-left: auto;
        padding: 0 15px;
        .partner__media_and_text__inner {
            display: flex;
            gap: 7%;
            position: relative;
            z-index: 1;
            align-items: center;
            &.partner__media_and_text__inner--left {
                flex-direction: row-reverse;
            }
            .partner__media_and_text-content {
                flex-basis: 50%;
                padding: 60px 40px;
                background-color: #fff;
                border-radius: 5px;
                margin-left: 5%;
                .md_bakery_partner_pre_text {
                    img {
                        width: 28px;
                        height: 28px;
                    }
                    h1 {
                        font-weight: 600;
                        color: #000;
                        position: relative;
                        font-size: 30px;
                        line-height: 1.3em;
                        margin: 0;
                    }
                    margin-bottom: 30px;
                }
                .description {
                    color: #000;
                    line-height: 1.8em;
                    font-weight: 500;
                    font-size: 16px;
                    margin-bottom: 1em;
                }
                .partner__media_and_text-buttons {
                    a {
                        min-width: 100px;
                        text-align: center;
                        display: inline-block;
                        cursor: pointer;
                        word-break: break-word;
                        text-decoration: none;
                        position: relative;
                        padding: 8px 20px;
                        margin: 0;
                        line-height: 1.4;
                        text-decoration: none;
                        background-color: rgba(0, 0, 0, 0);
                        font-size: 13px;
                        letter-spacing: 2px;
                        font-weight: 500;
                        color: #000;
                        border: none;
                        border-bottom: 1px solid #000;
                        border-radius: 0;
                        padding-left: 0;
                        padding-right: 0;
                    }
                }
            }
            .partner__media_and_text-media {
                flex-basis: 43%;
            }
        }
    }
    &.style2 {
        .media-and-text-section-two__top-wave {
            background-size: calc(100% + 2px) calc(100% + 2px) !important;
            background-repeat: no-repeat !important;
            background-position: 50% !important;
            overflow: hidden;
            bottom: 0;
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            transform: scale(-1, 1);
        }
        .partner__media_and_text__inner {
            gap: 4%;
            .partner__media_and_text-content {
                margin-left: 0;
                .partner__media_and_text-buttons {
                    margin: 40px 0 0;
                    a {
                        border-radius: 50px;
                        padding-left: 25px;
                        padding-right: 25px;
                        padding-top: 10px;
                        padding-bottom: 10px;
                        font-size: 14px;
                        font-weight: 500;
                        cursor: pointer;
                        display: block;
                        position: relative;
                        border: 2px solid #f75227;
                        transition: all .4s cubic-bezier(.42,0,.58,1);
                        display: inline-block;
                    }
                }
            }
            .partner__media_and_text-media {
                width: 50%;
                flex-basis: unset;
                img {
                    max-width: 65%;
                    display: block;
                    margin: 0 auto;
                    border-radius: 10px 0 10px 10px;
                    box-shadow: 0px 12px 18px -6px rgba(0, 0, 0, .3);
                    height: auto;
                }
            }
        }
    }
}

